import Vue from 'vue'

class VueTouch {
  /**
   * @param el DOM
   * @param binding binding
   * @param type 
   */
  constructor(el, binding, type) {
    let g = this;
    g.obj = el;
    g.binding = binding;
    g.touchType = type;
    g.firstTouchPosition = {
      x: 0,
      y: 0
    };
    g.firstTouchTime = 0;
    g.startFlag = ''; //mousedown/touchstart 
    /**
     * =========================================
     * 
     * @example
     *  1. v-tap="showDialog" 
     *  2. v-tap="{fn:click123, param1:1, param2:2, param3:{aaa:'123'} ...}"
     *      JSONfn
     * 
     * @param event
     * @param  binding.valuev-tap=""2 {fn:click123, param1:1, param2:2, param3:{aaa:'123'} ...}
     * =========================================
     */
    g.callBack = typeof (binding.value) === "object" ? binding.value.fn : binding.value;
    // 
    let _listener = Object.create(null);
    g._listener = _listener;
    // 
    let _listen = (type) => {
      return function (e) {
        /**
         * v-on
         */
        let {
          stop,
          prevent,
          self,
          once
        } = g.binding.modifiers;
        // 
        if (stop) e.stopPropagation();
        if (prevent) e.preventDefault();
        if (once) g.obj.removeEventListener("touch" + type, _listener[type]);
        if (self && e.target !== e.currentTarget) return;
        g[type](e);
      }
    };
    _listener.start = _listen('start');
    _listener.end = _listen('end');
    _listener.move = _listen('move');
    // 
    this.obj.addEventListener("touchstart", _listener.start, false);
    
    
    this.obj.addEventListener("mousedown", _listener.start, false);
    
    
  }
  // 
  start(e) {
    let g = this;
    // console.log('start--1',e.type,e);
    // mousedown  touchstart
    if(g.startFlag == ''){
      g.startFlag = e.type;
      // console.log('start--2',e.type,e);
      // @update 2018.3.26 
      g.moved = false; //
      g.leaved = false; // 
      g.longTouched = false; //
      // 
      g.firstTouchPosition = (e.type == 'mousedown') ? g.getMultiCenter({
        0: {
          pageX: e.pageX,
          pageY: e.pageY
        },
        length: 1
      }) : g.getMultiCenter(e.changedTouches);
      g.firstTouchTime = e.timeStamp;
      //  @TODO 
      g.time = setTimeout(()=>{
        // console.log('--1',g.leaved,g.moved);
        if (!g.leaved && !g.moved) {
          // console.log('--2');
          g.touchType === "longtap" && g.callBack(e, g.binding.value);
          g.longTouched = true;
        }
      }, 1000);
      g.obj.addEventListener("touchmove", g._listener.move, false);
      g.obj.addEventListener("mousemove", g._listener.move, false);

      g.obj.addEventListener("mouseup", g._listener.end, false);
      g.obj.addEventListener("touchend", g._listener.end, false);
      g.startFlag  = '';
    }
  }
  // 
  end(e) {
    // console.log('end',e);
    let g = this;
    // 
    let {
      x,
      y
    } = (e.type == 'mousedown') ? g.getMultiCenter({
      0: {
        pageX: e.pageX,
        pageY: e.pageY
      },
      length: 1
    }): g.getMultiCenter(e.changedTouches);
    let _disX = x - g.firstTouchPosition.x;
    let _disY = y - g.firstTouchPosition.y;
    let _dis = Math.sqrt(_disX * _disX + _disY * _disY);
    let _timeDis = e.timeStamp - g.firstTouchTime;
    clearTimeout(g.time);
    // 
    let _angle = this.getAngle(_disX, _disY);
    // 
    if (_dis > 30 && _timeDis < 300) {
      g.touchType === "swipe" && g.callBack(e, g.binding.value);
      if (_angle >= 60 && _angle <= 120)
        g.touchType === "swipedown" && g.callBack(e, g.binding.value);
      if (_angle <= -60 && _angle >= -120)
        g.touchType === "swipeup" && g.callBack(e, g.binding.value);
      if (_angle <= 20 && _angle >= -20)
        g.touchType === "swipeleft" && g.callBack(e, g.binding.value);
      if ((_angle <= -160 && _angle > -180) || (_angle >= 160 && _angle <= 180))
        g.touchType === "swiperight" && g.callBack(e, g.binding.value);
    } else {
      if (!g.longTouched && !g.moved && e.type == 'mouseup') {
        g.touchType === "tap" && g.callBack(e, g.binding.value);
        g.leaved = true;
      }
    }
  }
  move(event) {
    let g = this;
    if(!event.changedTouches){return}
    const newX = event.changedTouches[0].pageX - g.firstTouchPosition.x;
    const newY = event.changedTouches[0].pageY - g.firstTouchPosition.y;
    if((newX*newX+newY*newY)>900){
      this.moved = true;
    }
  }
  /**
   * 
   * @param touches touch
   * @return {{x: number, y: number}}
   */
  getMultiCenter(touches) {
    let x = 0,
      y = 0;
    touches = touches || {
      length: 0
    };
    const _length = touches.length;
    for (let i = 0; i < _length; i++) {
      x += touches[i].pageX;
      y += touches[i].pageY;
    }
    return {
      x: Math.round(x / _length),
      y: Math.round(y / _length)
    };
  }
  /**
   * 
   * @param disX X
   * @param disY Y
   * @return 
   */
  getAngle(disX, disY) {
    // let g = this;
    if (typeof disX !== 'number' || typeof disY !== 'number')
      return 45;
    return Math.atan2(disY, disX) * 180 / Math.PI;
  }
}

Vue.directive("tap", {
  bind: function (el, binding) {
    new VueTouch(el, binding, "tap");
  }
});
Vue.directive("swipe", {
  bind: function (el, binding) {
    new VueTouch(el, binding, "swipe");
  }
});
Vue.directive("swipeleft", {
  bind: function (el, binding) {
    new VueTouch(el, binding, "swipeleft");
  }
});
Vue.directive("swiperight", {
  bind: function (el, binding) {
    new VueTouch(el, binding, "swiperight");
  }
});
Vue.directive("swipedown", {
  bind: function (el, binding) {
    new VueTouch(el, binding, "swipedown");
  }
});
Vue.directive("swipeup", {
  bind: function (el, binding) {
    new VueTouch(el, binding, "swipeup");
  }
});
Vue.directive("longtap", {
  bind: function (el, binding) {
    new VueTouch(el, binding, "longtap");
  }
});